import React, { useContext } from 'react';
import { Button, Cascader, Form, Input, Radio }from 'antd';
import { authContext } from '../../context/AuthContext';
import { usersContext } from '../../context/UsersContext';

const UpdateUser = ({ user, setGetLastName, getLastName, setGetFirstName, getFirstName, setGetEmail, getEmail, setGetPhone, getPhone, setGetLevel, getLevel, setUsersArrayFiltered }) => {
    const { lastName, firstName, email, phoneNumber, level, _id} = user;
    const { setIsEdit, year  }=useContext(usersContext)
    const { reqBearer }= useContext(authContext)
   
   
    
    const updateLastName=(e)=>{
        setGetLastName(e.target.value)

    }
    const updateFirstName=(e)=>{
        setGetFirstName(e.target.value)

    }
    const updateEmail=(e)=>{
        setGetEmail(e.target.value)

    }
    const updatePhone=(e)=>{
        setGetPhone(e.target.value)

    }
    const updateLevel=(value)=>{
        setGetLevel(value)

    }
   
    const onFinish=()=>{
        console.log(getLastName)
        console.log(getFirstName)
        console.log(getEmail)
        console.log(getPhone)
        console.log(getLevel)
       
        reqBearer.put(
            `auth/user/update/${_id}`,
            {
                lastName: getLastName?getLastName.toUpperCase():lastName.toUpperCase,
                firstName: getFirstName? getFirstName: firstName,
                email: getEmail? getEmail:email,
                phoneNumber: getPhone? getPhone:phoneNumber,
                level: getLevel? getLevel[0]:level
            }
        )
        .then(()=>{
            alert(`Le profil de ${getFirstName} ${getLastName.toUpperCase()} a bien été modifié`)
            setIsEdit(false)
            setGetLastName("")
            setGetFirstName("")
            setGetEmail("")
            setGetPhone("")
            setGetLevel("")
          
        }) 
        
    }
    const onFinishFailed = (errorInfo) => {
        console.log('Failed:', errorInfo);
    };

    return (
        <div>
              <Form
                    onFinish={onFinish}
                    onFinishFailed={onFinishFailed}
                >
                   <Form.Item
                   label="Nom de famille"
                    initialValue={lastName.toUpperCase()}
                    name="lastName"                         
                    onChange ={updateLastName}
                    rules={[
                        {
                        required: true,
                        message: "Oups, vous avez oublié d'écrire un message!",
                        },
                        ]}
                    >
                        <Input/>
                    </Form.Item>
                   <Form.Item
                    initialValue={firstName}
                    label="Prénom"
                        name="firstName"                         
                        onChange ={updateFirstName}
                    rules={[
                        {
                        required: true,
                        message: "Oups, vous avez oublié d'écrire un message!",
                        },
                        ]}
                    >
                        <Input/>
                    </Form.Item>
                   <Form.Item
                        label="email"
                        name="email"
                        initialValue={email}                        
                        onChange ={updateEmail}
                    rules={[
                        {
                        required: true,
                        message: "Oups, vous avez oublié d'écrire un message!",
                        },
                        ]}
                    >
                        <Input/>
                    </Form.Item>
                   <Form.Item
                   label="Téléphone"
                    initialValue={0 +`${phoneNumber}`}
                        name="phoneNumber"                         
                        onChange ={updatePhone}
                    rules={[
                        {
                        required: true,
                        message: "Oups, vous avez oublié d'écrire un message!",
                        },
                        ]}
                    >
                        <Input/>
                    </Form.Item>
                   <Form.Item
                   label="Niveau d'adhésion"
                    initialValue={level}
                        name="level"  
                                        
                    >
                        <Cascader onChange ={updateLevel}     
                        options={[
                            {value:1, 
                            label:"1"},
                            {value:2, 
                            label:"2"},
                            {value:3, 
                            label:"3"}
                        ]}/>
                    </Form.Item>
                    <Form.Item  name="prolongation" label="Renouveler l'adhésion pour: ">
                    <Radio.Group >
                    <Radio value={year}>{year}</Radio>
                    <Radio value={year + 1}>{year + 1}</Radio>
                </Radio.Group>
                    </Form.Item>
                    <Button htmlType='submit'>Valider</Button>
                </Form>  
        </div>
    );
};

export default UpdateUser;